"use client";

import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useState } from "react";
import { useSelector } from "react-redux";

const companyArray = [];

const PostAJobPage = () => {
  const [input, setInput] = useState({
    title: "",
    description: "",
    requirements: "",
    salary: "",
    location: "",
    jobType: "",
    experience: "",
    position: 0,
    companyId: "",
  });

  const { companies } = useSelector((state) => state.company);

  const changeEventHandler = (e) => {
    setInput({ ...input, [e.target.name]: e.target.value });
  };

  const submitHandler = async (e) => {
    e.preventDefault();
  };

  return (
    <section className="flex items-center justify-center w-screen min-h-screen bg-gray-100 p-4">
      <div className="w-full max-w-2xl bg-white shadow-lg rounded-lg p-6 md:p-8">
        <h2 className="text-2xl font-bold mb-6 text-center md:text-3xl">
          Post a Job
        </h2>
        <form onSubmit={submitHandler} className="space-y-6">
          <div className="flex items-center gap-2">
            <div className="w-full">
              <Label className="block mb-2 text-sm font-medium text-gray-700">
                Title
              </Label>
              <Input
                type="text"
                name="title"
                value={input.title}
                onChange={changeEventHandler}
                placeholder="Job Title"
                className="w-full"
              />
            </div>
            <div className="w-full">
              <Label className="block mb-2 text-sm font-medium text-gray-700">
                Location
              </Label>
              <Input
                type="text"
                name="location"
                value={input.location}
                onChange={changeEventHandler}
                placeholder="Location"
              />
            </div>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-full">
              <Label className="block mb-2 text-sm font-medium text-gray-700">
                Salary
              </Label>
              <Input
                type="text"
                name="salary"
                value={input.salary}
                onChange={changeEventHandler}
                placeholder="Salary"
              />
            </div>
            <div className="w-full">
              <Label className="block mb-2 text-sm font-medium text-gray-700">
                Job Type
              </Label>
              <Input
                type="text"
                name="jobType"
                value={input.jobType}
                onChange={changeEventHandler}
                placeholder="Job Type"
              />
            </div>
          </div>
          <div className="flex items-center gap-2">
            <div className="w-full">
              <Label className="block mb-2 text-sm font-medium text-gray-700">
                Experience
              </Label>
              <Input
                type="text"
                name="experience"
                value={input.experience}
                onChange={changeEventHandler}
                placeholder="Experience"
              />
            </div>
            <div className="w-full">
              <Label className="block mb-2 text-sm font-medium text-gray-700">
                Position
              </Label>
              <Input
                type="number"
                name="position"
                value={input.position}
                onChange={changeEventHandler}
                placeholder="Position"
              />
            </div>
          </div>
          <div>
            <Label className="block mb-2 text-sm font-medium text-gray-700">
              Description
            </Label>
            <Input
              type="text"
              name="description"
              value={input.description}
              onChange={changeEventHandler}
              placeholder="Job Description"
            />
          </div>
          <div>
            <Label className="block mb-2 text-sm font-medium text-gray-700">
              Requirements
            </Label>
            <Input
              type="text"
              name="requirements"
              value={input.requirements}
              onChange={changeEventHandler}
              placeholder="Job Requirements"
            />
          </div>

          <div>
            <Label className="block mb-2 text-sm font-medium text-gray-700">
              Company ID
            </Label>
            <Input
              type="text"
              name="companyId"
              value={input.companyId}
              onChange={changeEventHandler}
              placeholder="Company ID"
            />
          </div>
          <div className="flex justify-end">
            <Button type="submit">Post Job</Button>
          </div>
          {companyArray.length === 0 && (
            <p className="text-xs text-red-600 font-bold text-center">
              *Please register a company, before posting a job
            </p>
          )}
        </form>
      </div>
    </section>
  );
};

export default PostAJobPage;
